.moleculeEditor{
    margin : 0;
    padding : 0;
    height : 100%;
    width : 100%;
    max-width: 1600px;
    max-height : 1200px;
}

.controlPanel{
    display : inline-block;
    vertical-align:top;
    width : 120px;
    height : 100%;
}

/*Custom radio button*/
.radioButtonAtomSelectMode {
    height : 32px;
    width : 32px;
    text-align: center;
}

.radioButtonAtomSelectMode > img {
    height : 32px;
    width : 32px;
}

.radioButtonAtomSelectMode > input {
    visibility: hidden;
    position: absolute;
}

.radioButtonAtomSelectMode > input + img {
    cursor: pointer;
    border : 2px solid transparent;
}

.radioButtonAtomSelectMode > input:checked + img {
    border : 2px solid #ff0000;
}


.controlButton {
    font-size : 14px;
    height : 100px;
    width : 100px;
    margin: 4px;
    background-color: bisque;
    text-align: center;
    line-height: 100px;
    display : block;
}

.displayPanel {
    display : inline-block;
    vertical-align:top;
    width : 800px;
    height : 600px;
}

.mainPanel{
    width : 100%;
}

/*
CSS for atomInfoTable
 */

.infoTables{

}

.atomInfoTable {
    display: inline-block;
    vertical-align: top;
    background : #34494E;
    color : #ffffff;
    border-radius: .4em;
    border-collapse: collapse;
    overflow : hidden;
    margin : 4px;
}

.atomInfoTable tr {
    border : 1px solid #44595E;
}

.atomInfoTable th,td {
    margin: .5em 1em;
    padding : .5em .5em;
}

.atomInfoRow {
    width : 100%;
    font-size : 14px;
    text-align: left;
}

.atomInfoRow > :first-child{
    width : 40px;
}

.atomInfoRow > :nth-child(2){
    width : 4em;
}

.atomInfoRow > :nth-child(3){
    width : 4em;
}

.atomInfoRow > :nth-child(1n+4){
    width : 80px;
}

/*
For distance/angle/dihedral table
 */

.connectInfoRow{
    width : 100%;
    font-size : 14px;
    text-align: left;
}

.connectInfoRow td:first-child{
    width : 5em;
}

.connectInfoRow td:nth-child(2){
    max-width : 10em;
}

.connectInfoRow td:nth-child(3){
    max-width : 10em;
}

.connectInfoRow td:nth-child(4){
    width : 5em;
}

.connectInfoRow td:nth-child(5){
    width : 5em;
}

.unitText {
    margin-left : 0.2em;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


.statusBar{
    width : 100%;
    font-size : 12px;
}


